.dashboardNav .connectWallet {
    background: rgba(19, 24, 35, .3411764705882353);
    box-shadow: 0 0 10px rgb(44 39 109 / 10%);
    border-radius: 10px;
    padding: 9px 20px;
    font-weight: 600;
    font-size: 14px;
    color: #fff;
}

.dashboardNav nav{
    height: 90px;
}

.dashboardNav .navbar-brand{
    color: #D0DCE8;
    font-size: 40px;
    font-weight: 700;
    width: 100px;
    height: auto;
}

.navbar-brand img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.dashboardNav .connectWallet:hover {
    background-color: rgba(255, 255, 255, 0.15) !important;
}

.dashboardNav .blog-link:hover {
    background: rgba(19, 24, 35, .3411764705882353);
    box-shadow: 0 0 10px rgb(44 39 109 / 10%);
}

.dashboardNav .toggle-btn {
    background: rgba(19, 24, 35, .3411764705882353);
    box-shadow: 0 0 10px rgb(44 39 109 / 10%);
    border-radius: 10px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.dashboardNav .dropdown-menu {
    background-color: rgba(19, 24, 35, .3411764705882353);
}

.dashboardNav .dropdown-item {
    color: #D0DCE8;
}

.dashboardNav .dropdown-item:hover{
    background-color: transparent;
}

@media (max-width : 767px) {
    .dashboardNav .connectWallet{
        font-size: 11px;
    }
    .dashboardNav .navbar{
        padding: 0;
    }
}