
.Dashboard {
    margin-top: 55px;
}

.Dashboard .dashboard-stats{
    text-align: center;
    background: rgba(19,24,35,.3411764705882353);
    -webkit-backdrop-filter: blur(33px);
    backdrop-filter: blur(33px);
    border-radius: 12px;
    border: 1px solid rgba(19,207,207,.06666666666666667);
    padding: 20px 30px 30px 30px;
}

.dashboard-stats .stats-title{
    font-weight: 500;
    font-size: 1rem;
    color: rgba(255, 255, 255, 0.6);
    padding: 3px 0;
}

.dashboard-stats .stats-update{
    font-size: 1.3118rem;
    font-weight: 700;
    color: #fff;
}

@media (max-width: 1399px) {
    .dashboardNav .blog-link{
        right: 200px;
    }

    .Dashboard{
        overflow-y: auto;
        height: calc(100vh - 206px);
    }

    .Dashboard::-webkit-scrollbar{
        width: 1px;
    }
    
}

@media (max-width : 767px) {
    .Dashboard{
        overflow-y: auto;
        height: calc(100vh - 120px);
    }

    .dashboard-stats .stats-update, .dashboard-stats .stats-title{
        font-size: 12px;
    }

    
}