.Account {
  margin-top: 55px;
}

.Account .account-card {
  color: rgba(243, 240, 240, .87);
  transition: box-shadow .3s cubic-bezier(.4, 0, .2, 1) 0ms;
  background: rgba(19, 24, 35, .3411764705882353);
  justify-content: center;
  padding: 20px;
  border-radius: 20px;
  border: 1px solid rgba(19, 207, 207, .06666666666666667);
}

.account-card h4 {
  color: rgb(255, 255, 255);
  font-weight: 700;
  font-size: 24px;
}


.account-card .loaderText {
  height: 16px;
  transform: scale(1, 0.60);
  margin-top: 0;
  border-radius: 4px;
  margin-bottom: 0;
  transform-origin: 0 60%;
  animation: shadow-pulse 1.5s ease-in-out 0.5s infinite;
  transition: all ease-in-out;

}


.account-card .loader {
  background-color: rgba(208, 220, 232, 0.13);
  height: 20px;
}


@keyframes shadow-pulse {
  0% {
    background-color: rgba(208, 220, 232, 0.13);
  }

  50% {
    background-color: rgba(208, 220, 232, 0.50);
  }

  75% {
    background-color: rgba(208, 220, 232, 0.75);
  }

  100% {
    background-color: rgba(208, 220, 232, 0.13);
  }
}


@media (max-width: 1399px) {
  .Account {
    overflow-x: hidden;
    overflow-y: auto;
    height: calc(100vh - 206px);

  }

  .Account::-webkit-scrollbar {
    width: 1px;
  }

  .Account .fs-20 {
    font-size: 18px;
  }



}


@media (max-width : 767px) {

  .Account {
    overflow-y: auto;
    height: calc(100vh - 120px);
  }

  .fs-16 {
    font-size: 12px;
  }

}