.sideBar .side-bar {
    width: 300px;
    padding: 0 30px;
    padding-top: 75px;
}


.side-bar li:first-child {
    margin-bottom: 40px;
}

.side-bar li:nth-child(n + 2) {
    margin-bottom: 30px;
}

.side-bar a.active {
    color: #26E2B3;
    border: 1px solid #13cfcf11;
    background: #13182357;
    box-shadow: 12px 12px 22px rgb(0 0 0 / 25%);
    border-radius: 63px;
    backdrop-filter: blur(15px);

}

.side-bar a {
    color: #fff;
    margin: 0 auto;
    padding: 5px 44px 5px 20px;
    font-size: 1rem;
    font-weight: 500;
    line-height: 1.6;
}

.side-bar a:hover {
    color: #26E2B3;
}


.side-bar a:hover {
    text-decoration: none;
}

.sideBar .social-icon {
    display: flex;
    margin-top: 100px;
    margin-left: 45px;
}

.sideBar .social-icon li:not(:last-child) {
    margin-right: 20px;

}

.social-icon a {
    color: #D0DCE8;
    width: 30px;
    height: 30px;
}

.social-icon a:hover i {
    color: #28E0B2;
}

.social-icon a i {
    font-size: 20px;
}




@media (max-width: 1399px) {
    .sideBar {
        overflow-x: hidden;
        overflow-y: auto;
        height: calc(100vh - 206px);
    }

}

@media (max-width: 992px) {
    .sideBar {
        position: absolute;
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter: blur(5px);
        z-index: 11;
        height: calc(100vh - 30px);
        min-width: 290px;
        left: 0;
        top: 0;
        display: none;
    }
}

@media (max-width: 992px) {
    .sb-mobile {
        position: absolute;
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter: blur(5px);
        z-index: 11;
        height: calc(100vh - 30px);
        min-width: 290px;
        left: 0;
        top: 0;
        display: block;
    }

    .sideBar .toggle {
        position: absolute;
        top: 10px;
        right: 10px;
        display: block;
        font-size: 30px;
        font-weight: 600;
        color: #ccc;
    }
}