.Swap a:hover {
    text-decoration: none;
    color: inherit;
}

.Swap .swapCard {
    background: rgba(19, 24, 35, .3411764705882353);
    color: #D0DCE8B2;
    -webkit-backdrop-filter: blur(40px);
    backdrop-filter: blur(40px);
    position: relative;
    z-index: 2;
    border-radius: 10px;
    padding: 20px;
    width: 100%;
}

.swapCard .inputDrop {
    position: relative;
    width: 100%;
    height: 70px;
    background: rgba(255, 255, 255, .08);
    border-radius: 12px;
    padding: 0.8rem 1rem;

}

.inputDrop .firstInput {
    width: 120px;
    height: 100%;
    text-align: right;
    position: absolute;
    top: 0;
    /* left: 0; */
    right: 0;
    bottom: 0;
    background-color: transparent;
    border: 0;
    padding: 10px;
    color: #D0DCE8B2;
    z-index: 1;
}

input:focus {
    outline: 0;
}

.swapCard .dropdown-menu {
    background: gray;
    min-width: 109%;
    padding: 0.5em;
    border: 1px solid rgba(0, 0, 0, .15);
    border-radius: 0.25rem;
    top: 12px !important;
    left: -15px !important;
}

.swapCard .dropdown-item {
    padding: 0.8em;
    border-radius: 8px;
}

.swapCard .dropdown-item:hover {
    cursor: pointer;
    background: rgba(255, 255, 255, .08);
}

.inputDrop .dropDown-search {
    width: 100%;
    height: 40px;
    position: relative;
    display: flex;
}

.dropDown-search input {
    background: rgba(255, 255, 255, .08);
    border-radius: 8px 0 0 8px;
    padding: 0.6em;
    width: 90%;
    text-align: left;
    border: 0;
    padding: 10px;
    color: #D0DCE8B2;

}

.dropDown-search button {
    width: 10%;
    background-color: #29fde8;
    color: #fff;
    border-radius: 0 8px 8px 0;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.swapCard .dropdown-menu .icon{
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.dropdown-item .icon img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 50%;
}

.swapCard .scroll{
    height: auto;
    max-height: 340px;
    overflow-x: hidden;
}

.swapCard .footerText{
    text-align: center;
    margin-top: 30px;
}

.swapCard .swapRound{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, .08);
    border-radius: 50%;
    color: #fff;
    cursor: pointer;
}

.btnSwap{
    background-color: #29fde8;
    color: #eb0066;
    display: block;
    font-weight: 700;
}

.cp{
    cursor: pointer;
}

.btnSwap:hover{
    color: #eb0066;
    background-color: rgba(41, 253, 232, 0.8);
}