.Calculator {
    margin-top: 55px;
}

.Calculator .calculator-card {
    background: rgba(19, 24, 35, .3411764705882353);
    -webkit-backdrop-filter: blur(40px);
    backdrop-filter: blur(40px);
    position: relative;
    z-index: 2;
    border-radius: 10px;
    padding: 20px;
    width: 100%;
}

.calculator-card .metric-title {
    font-weight: 500;
    font-size: 20px;
    color: hsla(0, 0%, 100%, .6);
    margin-bottom: 2px;
}


.calculator-card .metric-value {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: #fff;
}

.calculator-card label {
    font-weight: 500;
    font-size: 16px;
    color: #fff;
    margin-bottom: 5px;
    margin-left: 10px;
}

.calculator-card .input-with-value {
    position: relative;
    width: 100%;
    border: 1px solid hsla(0, 0%, 100%, .2);
    border-radius: 10px;
    outline: none;
}


.calculator-card .input-with-value input {
    width: 100%;
    background-color: transparent;
    outline: 0;
    border: 0;
    font-weight: 500;
    font-size: 16px;
    color: hsla(0, 0%, 100%, .6);
    padding: 14px 20px;

}


.input-with-value .second-value {
    position: absolute;
    top: 13px;
    right: 25px;
    font-weight: 600;
    font-size: 18px;
    color: #fff;
}

.calculator-card .inputRange .input-range__slider {
    width: 20px;
    height: 20px;
    background: #232323;
    box-shadow: 0 0 10px rgb(0 0 0 / 20%);
    border: 2px solid 2px solid #446cf6;
}

.calculator-card .inputRange .input-range__track--active {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    background: 2px solid linear-gradient(90deg, #64c9fc, #446cf6 90%);
    box-shadow: 0 0 10px rgb(0 0 0 / 20%);
    height: 100%;
    width: 100%;
}

.calculator-card .inputRange .input-range__label--max,
.calculator-card .inputRange .input-range__label--min {
    display: none;
}

.calculator-card .inputRange .input-range__track--background {
    border: 1px solid #ccc;
    background-color: transparent;
    height: 10px;

}

.calculator-card .inputRange .input-range__label--value {
    top: -2.6rem;
}

.calculator-card .inputRange .input-range__slider {
    margin-top: -0.95rem;
}

@media (max-width: 1399px) {
    .Calculator {
        overflow-x: hidden;
        overflow-y: auto;
        height: calc(100vh - 206px);
    }

    .Calculator::-webkit-scrollbar {
        width: 1px;
    }

}

@media (min-width : 280px) and (max-width: 767px) {

    .Calculator {
        overflow-y: auto;
        height: calc(100vh - 120px);
    }

    .calculator-card .metric-title {
        font-size: 13px;
    }

    .calculator-card .metric-value {
        font-size: 15px;
    }

}