.percentage{
  background: rgba(19,24,35,.3411764705882353);
  -webkit-backdrop-filter: blur(33px);
  backdrop-filter: blur(33px);
  border-radius: 21.2895px;
  border: 1px solid rgba(19,207,207,.06666666666666667);
  box-shadow: 11.4753980637px 11.4753980637px 11.4753980637px 0 rgb(0 0 0 / 25%);
  color: #26e2b3;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75px;
  height: 35px;
  cursor: pointer;
}

.textGreen{
  color: #26e2b3;
}

.textGray{
  color: #D0DCE8B2;
}

::-webkit-scrollbar {
  display: none!important;
}

.glow-green{
  box-shadow: -5px -5px 30px 5px #29fee8, inset 5px 5px 30px 5px #29fee8;
  border: 4px solid #29fee8;
}
.glow-pink{
  box-shadow: -5px -5px 30px 5px #ea0064, inset 5px 5px 30px 5px #ea0064;
  border: 4px solid #ea0064;
}